@import "../../units";

#menuToggle {

  input {
    display: none;
  }

  span {
    display: none;
    width: 32px;
    height: 2px;
    margin-bottom: 8px;
    border-radius: 2px;
    z-index: 3;
    transform-origin: 2px 0;
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1) opacity 0.55s;

    &:first-child {
      transform-origin: 0% 0%;
    }
    &:nth-last-child(2) {
      transform-origin: 0% 100%;
    }
  }
}

.nav {
  @extend %flex-centered;

  &__list {
    display: flex;
    list-style: none;
  }

  &__item {
    padding: 0 32px;
  }

  &__link {
    @extend %text-small;
    font-weight: bold;
    text-decoration: none;
    transition: 0.3s;
    text-transform: uppercase;
    padding: 21px 0;
    border-bottom: 2px solid $white;

    &:hover {
      color: $text-primary;
      border-bottom: 3px solid $text-primary;
    }

    &--active {
      color: $text-primary;
      border-bottom: 3px solid $text-primary;
    }
  }
}

